function Length ({title,timeChange,type,time,timeFormat}){
    return(
        <div class="handlers">
            <h3>{title}</h3>
            {
                (type=='break')?
            <div id="break-handler">
                <button  id="break-decrement" 
                    onClick={()=>timeChange(-60,type)}>
                    <i className="material-icons">↓</i>
                </button>
                <h3 id="break-length">{timeFormat(time)}</h3>
                <button  id="break-increment" 
                onClick={()=>timeChange(60,type)}>
                    <i className="material-icons">↑</i>
                </button>
            </div>
            :
            <div id="session-handler">
                <button id="session-decrement" 
                    onClick={()=>timeChange(-60,type)}>
                    <i className="material-icons">↓</i>
                </button>
                <h3 id="session-length">{timeFormat(time)}</h3>
                <button id="session-increment" 
                onClick={()=>timeChange(60,type)}>
                    <i className="material-icons">↑</i>
                </button>
            </div>
            }
        </div>
    )
}

export default Length